import { ThunkAction } from 'redux-thunk'
import SpotifyWebApi from 'spotify-web-api-js'
import * as t from 'io-ts'
import { IoMdStar } from 'react-icons/io'

export interface StringDict<T> {
    [id: string]: T
}

export interface NumberDict<T> {
    [id: number]: T
}

export const PositionType = t.type({
    x: t.number,
    y: t.number
})

export type Position = t.TypeOf<typeof PositionType>

export interface SongFull {
    artistsIds: string[]
    externalUrl: string
    id: string
    name: string
    previewLink: string
}

export interface SongReduced {
    id: string
    name: string    
}

export interface ArtistFull {
    genres: string[]
    id: string
    image: string
    isFollowed: boolean
    likedSongs: SongFull[]
    name: string
    relatedArtistsIds: string[]
    score: number
}

export interface ArtistReduced {
    genres: string[],
    id: string,
    relatedArtistsIds:string[]
}

export interface ArtistSimplified {    
    id: string,
    name: string
}

export interface ArtistEdge {
    id1: string
    id2: string
    weight: number
}

export interface Cluster {
    id: string
    genres: string[]
    artistsIds: string[]
    artistsPositions: StringDict<Position>
    country?: string
}

export interface ClusterEdge {
    id1: string
    id2: string
    weight: number
}

export interface ClusterSquare {
    [clusterId: string]: Position
}

export interface Graph {
    clusters: Cluster[]
    clusterEdges: ClusterEdge[]
    artistDict: StringDict<ArtistFull>
    artistEdges: ArtistEdge[]
    clusterSquares: ClusterSquare[]
}

export interface ChosenArtistData {
    notLikedSongs: SongFull[]
    likedSongs: SongFull[]
}

export interface State {
    chosenCluster?: Cluster

    chosenArtist?: ArtistFull
    chosenArtistData?: ChosenArtistData
    chosenArtistError?: string
    chosenArtistLoading: boolean

    graph: Graph

    // access variables
    userLoggedIn: boolean
    accessToken?: string

    // the song being played
    songToPlay?: SongFull
    nonFavouriteArtistDict: StringDict<ArtistSimplified>

    // the state indicating the graph loading process
    graphLoaded: boolean
    graphLoading: boolean
    isGraphLoadingError: boolean
    graphLoadingErrorMessage?: string

    graphLoadingMessage: string
    graphLoadingRelatedArtistsProgress: number

    isGraphFromFileLoaded: boolean

    // prepared spotify web api for requests:
    spotifyWebApiObject: SpotifyWebApi.SpotifyWebApiJs

    // player state
    paused: boolean
}

export interface ActionWrapper<Payload = undefined> {
  type: string
  payload?: Payload
  reducer: (state: State) => State
}

export type ThunkActionWrapper<T> = ThunkAction<Promise<T>, State, {}, ActionWrapper<any>>
