export const setSongToPlay = (song: SongFull): ActionWrapper<SongFull> => ({
    type: 'SET_SONG_TO_PLAY',
    payload: song,
    reducer: produce((state: State) => {
        if (!!!state.songToPlay || state.songToPlay.id !== song.id) {
            state.songToPlay = song
            state.paused = false
        }
    })
})

